import React from 'react';
import BasicSelect from '../../Atoms/Select/BasicSelect';
import MenuItem from '@mui/material/MenuItem';
import RankImage from '../../../common/Rank/RankImage';

const selectItemStyle = {
  width: '100%',
  height: '30px',
  display: 'flex',
  alignItems: 'center',
  padding: '0 30px',
  justifyContent: 'space-between',
  '&:hover': {
    fontWeight: 'bold',
    backgroundColor: '#f3f3f3',
  },
  '&:focus': {
    fontWeight: 'bold',
  },
};

const RankSelect = ({ value, onChange, ...props }) => {
  // 급 data
  const rankData = [
    { id: 9, title: '9급' },
    { id: 8, title: '8급' },
    { id: 7, title: '7급' },
    { id: 6, title: '6급' },
    { id: 5, title: '5급' },
    { id: 4, title: '4급' },
    { id: 3, title: '3급' },
    { id: 2, title: '2급' },
    { id: 1, title: '1급' },
    // { id: 101, title: '1품' },
    // { id: 102, title: '2품' },
    // { id: 103, title: '3품' },
    // { id: 104, title: '4품' },
    // { id: 1001, title: '1단' },
    // { id: 1002, title: '2단' },
    // { id: 1003, title: '3단' },
    // { id: 1004, title: '4단' },
    // { id: 1005, title: '5단' },
    // { id: 1006, title: '6단' },
    // { id: 1007, title: '7단' },
    // { id: 1008, title: '8단' },
    // { id: 1009, title: '9단' },
  ]
  ;

  const handleChange = (e) => {
    const event = { target: { name: props.name, value: (e.target.value) } };
    onChange(event);
  };

  return (
    <>
      <BasicSelect
        size="small"
        name='level'
        value={value}
        label={props.label}
        onChange={handleChange}
        minWidth="160px"
        className="bg-[#fff]"
        {...props}
      >
        <MenuItem disabled value="" sx={{ display: 'none' }} />
        {React.Children.toArray(
          rankData?.map(({ id, title }) => (
            <MenuItem className="h-[40px]" value={id} sx={selectItemStyle}>
              {title} <RankImage src={title} />
            </MenuItem>
          )),
        )}
      </BasicSelect>
    </>
  );
};

export default RankSelect;
