import React from 'react'
import { useQuery } from '@tanstack/react-query'
import { useRecoilValue } from 'recoil'
import dojangIdAtom from '../../../../../../recoil/atom/dojangIdAtom'
import { getStudent } from '../../../../../../api/Student/studentApi'

export const useStudent = (classes) => {
  const dojangId = useRecoilValue(dojangIdAtom)
  
  return useQuery(['student', classes, dojangId], () => getStudent(classes, dojangId), {
    enabled: !!dojangId && !!classes,
  })

}
