import React from 'react'
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import theme from '../../../../themes';

const SelectStyle = {
  '& .MuiSelect-select': {
    border: `1px solid ${theme.select.basic.border}`,
    display: 'flex',
    alignItems: 'center',
    gap: '15px',
    '&:hover': {
      borderColor: theme.select.basic.hover.border,
    },
    '&.Mui-focused': {
      border: `1px solid ${theme.select.basic.focus.border}`,
    },
    '&.Mui-disabled': {
      borderColor: theme.select.basic.disabled.border,
    },
  },
}

const BasicSelect = ({ minWidth, size, label, placeholder, defaultValue, value, onChange, children, ...props }) => {
  

  return (
    <div>
      <FormControl sx={{ minWidth: minWidth }} size={`${size}`}>
        <InputLabel id="label">{label}</InputLabel>
        <Select
          placeholder={placeholder}
          defaultValue={defaultValue}
          value={value}
          onChange={onChange}
          label={label}
          sx={SelectStyle}
          {...props}
        >
          { children }
        </Select>
      </FormControl>
    </div>
  );
}

export default BasicSelect

